.trees-table{
    justify-content: center;
    margin-top: 50px;
    direction: rtl;
    table{
        // border: 1px solid black;
        border-spacing: 0px;
        tr{
            th{
                border: 1px black solid;
                text-align: center;
                padding: 6px;
                background-color: #16a53f;
                color: white;
            }
            
            td{
                text-align: center;
                padding: 3px;
                border: 1px black solid;
            }
        }
    }
}